.overlay {
	position: absolute;
	left: 0;
	top: 0;
	background-color: rgba(0, 0, 0, 0.1);
	z-index: 1;
	width: 100%;
	height: 100%;
	visibility: hidden;
	opacity: 0;
	transition: opacity 0.5s ease-in-out, visibility 0.3s ease-in;
   
    transition: transform .3s ease-in-out;
}
.overlayVisible {
	visibility: visible;
	opacity: 1;
    .drawer {
        transform: translateX(0%);
    }
}
.drawer {
	display: flex;
	flex-direction: column;
	padding: 30px;
	position: absolute;
	width: 420px;
	right: 0;
	height: 100%;
	background-color: #fff;
	box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
	h2 {
		margin: 0;
		font-size: 24px;
	}
	.items {
		flex: 1;
		overflow: scroll;
		height: 400px;
	}
}
